@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;1,500;1,600;1,700&display=swap");

* {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

.background__img {
  z-index: -1;
}
.salesPage__main .hero__section {
  border-radius: 16px;
  background: #ddd3abb9;
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 32px;
  margin: 12vh 10vw 12vh 64px;
}

.salesPage__main .hero__section i {
  color: #f49365;
}

.salesPage__main .hero__section .left__section {
  position: relative;
}

.hero__section .left__section img {
  position: absolute;
  left: 0%;
  top: 50%;
  transform: translate(-3%, -50%);
  width: 25vw;
}

.salesPage__main .hero__section .right__section {
  padding: 64px;
  position: relative;
}

.salesPage__main .hero__section .right__section img {
  position: absolute;
  z-index: -1;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 45%;
}

.salesPage__main .hero__section .right__section .title {
  margin-bottom: 32px;
}

.salesPage__main .hero__section .right__section .content {
  position: relative;
  z-index: 3;
  font-size: 18px;
}

.salesPage__main .hero__section .right__section p {
  margin-bottom: 8px;
}

.salesPage__main .features {
  padding: 16px 10%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f2af27;
  margin-bottom: 32px;
}

.salesPage__main .feature {
  display: flex;
  align-items: center;
  justify-content: center;
}

.salesPage__main .feature img {
  margin-right: 8px;
  width: 50px;
}

.salesPage__main .prices__heading {
  text-align: center;

  margin-bottom: 10vh;
}

.salesPage__main .prices__heading p {
  font-size: 32px;
  margin-top: 16px;
  margin-bottom: 32px;
}

.salesPage__main .prices {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 16px;
  justify-content: center;
  margin: 32px 10% 34px;
  /* height: 550px;
  max-height: 550px; */
  position: relative;
}

.salesPage__main .plan-container {
  margin: auto;
  position: relative;
}

.salesPage__main .plan-details {
  position: relative;
  color: white;
  padding: 16px;
  padding-bottom: 32px;
  border-radius: 12px;
  z-index: 2;
  margin: 0 6px;
  min-height: 400px;
}

.salesPage__main .plan .details ul {
  list-style: none;
  padding-top: 8px;
  padding-left: 24px;
}

.salesPage__main .plan .details ul li {
  margin-bottom: 8px;
}

.salesPage__main .plan-details.red {
  background: #f05d6a;
}

.salesPage__main .plan-details.yellow {
  background: #ffd27b;
}

.salesPage__main .plan-details.green {
  background-color: #36bfa5;
}

.salesPage__main .plan-details .heading {
  text-align: center;
  margin-bottom: 0;
}

.salesPage__main .marquee {
  width: 60%;
  margin: auto;
}

.salesPage__main marquee {
  width: 100%;
  height: 100px;
}

.salesPage__main .plan-details .add-to-cart {
  text-align: right;
}

.salesPage__main .plan-details .heading h2 {
  font-weight: normal;
  font-size: 28px;
}
.salesPage__main .plan-details .add-to-cart a {
  color: white;
  text-decoration: none;
}

.salesPage__main .plan-container .black {
  position: relative;
  background-color: black;
  opacity: 0.7;
  height: 12px;
  z-index: 1;
  margin-top: -350px;
  border-radius: 0 0 12px 12px;
  margin-bottom: -5px;
  transition: 0.7s;
}

.salesPage__main .plan-cover {
  position: relative;
  background: #eeeeee;
  text-align: center;
  padding: 32px;
  z-index: 3;
  min-height: 300px;
  border-radius: 0 0 12px 12px;
}

.salesPage__main .plan-cover h1 {
  font-weight: normal;
  font-size: 53px;
}

.salesPage__main .plan-cover .price {
  margin-top: 8vh;
}

.salesPage__main .plan-cover .price .atc {
  margin-top: 8px;
}

.salesPage__main .plan-container:hover .black {
  margin-top: -20px;
}

.salesPage__main .payment {
  text-align: center;
}

.salesPage__main .payment a {
  display: block;
  width: 12%;
  padding: 8px 16px;
  border: 1px solid black;
  border-radius: 16px;
  margin-right: 64px;
  margin-left: auto;
  color: black;
  text-decoration: none;
}

.salesPage__main .for__school .heading {
  text-align: center;
}

.salesPage__main .for__school .heading h1 {
  font-weight: 400;
}

.salesPage__main .for__school .content img {
  width: 30%;
}

.salesPage__main .for__school .content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 75%;
  margin: auto;
  margin-top: 32px;
  margin-bottom: 64px;
}

.salesPage__main .for__school .contentt {
  padding: 32px;
  background: #eeeeee;
  border-radius: 16px;
  margin-left: -64px;
  min-height: 250px;
}

.salesPage__main .for__school .contentt p {
  font-size: 19px;
}

.salesPage__main .for__school .contentt a {
  display: block;
  text-decoration: none;
  color: #00b2ff;
  margin-top: 8vh;
}
.class_selection_overlay {
  width: 100vw;
  height: 100vh;

  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(128, 128, 128, 0.6);
  z-index: 900;
}

@media (max-width: 1100px) {
  .salesPage__main .prices {
    margin: 32px 5% 34px;
  }
  .salesPage__main .hero__section .right__section img {
    width: 60%;
  }
  .hero__section .left__section img {
    position: relative;
    left: 10%;
    transform: translate(0%, -50%);
    width: 100%;
  }

  .salesPage__main .for__school .content {
    display: grid;
    grid-template-columns: minmax(0, 1fr);
  }

  .salesPage__main .for__school .content img {
    width: 50%;
    margin: auto;
    border-radius: 8px;
  }

  .salesPage__main .for__school .contentt {
    margin-left: 0;
    width: 60%;
    margin: auto;
    margin-top: -3%;
  }
  .background__img {
    width: 50%;
  }
}

@media (max-width: 1023px) {
  .salesPage__main .hero__section {
    grid-template-columns: 1fr;
  }

  .hero__section .left__section {
    text-align: center;
  }

  .hero__section .left__section img {
    position: relative;
    left: 0%;
    top: 0;
    transform: none;
    width: 50%;
    margin-top: 32px;
  }
}

@media (max-width: 850px) {
  .salesPage__main .prices__heading p {
    margin: 16px;
  }

  .salesPage__main .prices {
    grid-template-columns: 1fr;
    width: 60%;
    margin: auto;
  }
}

@media (max-width: 767px) {
  .salesPage__main .features {
    flex-direction: column;
  }

  .salesPage__main .features .feature {
    margin-bottom: 16px;
  }

  .salesPage__main .for__school .content {
    margin: 32px auto;
  }
}

@media (max-width: 500px) {
  .salesPage__main .hero__section {
    width: 90%;
    margin: 32px auto;
  }

  .salesPage__main .hero__section .right__section {
    padding: 32px;
  }

  .hero__section .left__section img {
    width: 75%;
  }

  .salesPage__main .prices {
    width: 85%;
  }

  .salesPage__main .for__school .content img {
    width: 90%;
    margin: auto;
    border-radius: 8px;
  }

  .salesPage__main .for__school .contentt {
    margin-left: 0;
    width: 80%;
    margin: auto;
    margin-top: -3%;
  }

  .salesPage__main .for__school .content {
    margin: 32px auto;
  }

  .salesPage__main .plan-cover h1 {
    font-weight: normal;
    font-size: 40px;
  }
}
