* {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
}

.navigation {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  align-items: center;
  height: 80px;

  background-color: white;
}
.dropdown {
  z-index: 10;
}

.navigation .link {
  display: flex;
  justify-content: space-evenly;

  flex: 1 1;
  max-width: 600px;
  height: 100%;
}
.navigation .logo__container {
  /* width: 200px; */
  width: 50%;
  flex: 1 1;
  height: 100%;
  overflow: hidden;
}

.navigation .logo {
  /* width: 200px; */
  height: 100%;
}
.navigation > .logo__container > a {
  display: inline-block;
  height: 100%;
}
.navigation .logo__container img {
  height: 100%;
}

.navigation .link a,
.mobile__links a {
  text-align: center;
  text-decoration: none;
  font-size: 14px;
  color: #6e6e6e;
  font-family: "Lato", sans-serif;
  margin-right: 32px;
}

.navigation .link a p {
  margin-top: -4px;
}

.mobile__links {
  display: flex;
  max-height: 0px;
  overflow: hidden;
  transition: 0.5s;
  margin-left: 32px;
}

.mobile__links.active {
  max-height: 300px;
}

.navigation .bars {
  display: flex;
  align-items: center;
  font-size: 30px;
  cursor: pointer;
  display: none;
}

.getInTouch {
  padding: 32px;
  border-top: 2px solid white;
  border-bottom: 2px solid white;
  display: flex;
  color: white;
  font-family: "Montserrat", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
}

.getInTouch p {
  font-size: 28px;
}

.getInTouch .input {
  display: flex;
  align-items: center;
}

.getInTouch input {
  padding: 16px 64px 16px 32px;
  border-radius: 26px;
  border: 0;
  outline: 0;
  margin: 8px;
  width: 500px;
  font-size: 20px;
  margin-left: 64px;
}

.getInTouch img {
  margin-left: -56px;
}

.madeWith {
  text-align: center;
  color: white;
  font-size: 18px;
  margin-top: 32px;
}

.footer .links {
  display: flex;
  padding: 32px;
  justify-content: space-between;
}

.footer .links .social {
  display: flex;
  align-items: center;
}

.footer .links .social a {
  display: block;
  margin-right: 16px;
}

.footer .links ul {
  list-style: none;
}

.footer .links a {
  color: white;
  text-decoration: none;
}
.mobile__links {
  display: none;
}

@media (max-width: 1000px) {
  .navigation {
    padding: 10px;
    padding-right: 0;
  }

  .navigation .logo {
    /* width: 200px !important;
    height: 79px !important; */
  }
  .navigation .link a {
    margin-right: 20px;
  }

  .getInTouch input {
    width: 300px;
  }

  .footer .social img {
    width: 24px;
  }
}

@media (max-width: 767px) {
  .navigation {
    padding: 0 16px;
  }

  .navigation .logo {
    /* width: 100px !important; */
    height: 98% !important;
  }

  .navigation .home_image {
    width: 100px !important;
    height: 39.5px !important;
  }

  .navigation .link {
    display: none;
  }

  .navigation .bars {
    display: flex;
  }

  .getInTouch input {
    width: 50%;
  }
  .mobile__links.active {
    display: flex;
    flex-wrap: wrap;
  }
}

@media (max-width: 600px) {
  .getInTouch {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .getInTouch .input {
    justify-content: center;
  }

  .getInTouch input {
    margin-left: 0;
    width: 100%;
    padding: 8px 64px 8px 32px;
  }

  .footer .madeWith p {
    font-size: 14px;
  }

  .footer .social img {
    width: 22px;
  }

  .footer .terms a {
    font-size: 14px;
  }
}

@media (max-width: 424px) {
  .footer .links {
    flex-direction: column-reverse;
  }

  .footer .social {
    margin-top: 16px;
  }
}

* {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.signup__container {
  display: grid;
  grid-template-columns: minmax(0, 1.5fr) 1fr;
  background: #edeeef;
  min-height: 88vh;

}

.circular__carosel {
  background: no-repeat center center fixed;
  background-size: cover;
  position: relative;
  margin-left: 32px;
  /* min-height: 85vh; */
}

.login__signup {
  padding: 64px;
}

.signup__container .title {
  display: flex;
  justify-content: space-between;
  width: 60%;
  margin: auto;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.signup__container .title h1 {
  font-size: 24px;
  font-weight: 500;
  padding-bottom: 8px;
  border-bottom: 4px solid transparent;
  cursor: pointer;
  transition: 0.5s;
}

.signup__container .Login_Signup_link {
  color: #3d4372;
  text-decoration: none;
}
.signup__container .title h1 a {
  color: #3d4372;
  text-decoration: none;
}

.signup__container .title h1.active,
.signup__container .title h1:hover {
  border-bottom: 4px solid #f06f38;
}

.signup__container .content {
  width: 80%;
  margin: auto;
  margin-top: 40px;
  display: none;
}

.signup__container .content.active {
  display: block;
}

.signup__container .content form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.signup__container form input {
  width: 100%;
  background: transparent;
  border: 0;
  outline: 0;
  padding: 16px;
  border-bottom: 1px solid #787878;
  font-size: 16px;
  padding-left: 0;
  margin-bottom: 32px;
  color: #787878;
}

.signup__container .submit {
  margin-top: 64px;
  background: white;
  width: 129px;
  height: 40px;
  border-radius: 24px;
  border: 0;
  outline: 0;
  font-weight: bold;
  font-size: 16px;
  cursor: pointer;
  border : 1px solid #f06f38;

}

