circular__carosel */

.signup__container .carousel li {
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  list-style: none;
  transition: 1s;
}

.signup__container .carousel li.active {
  top: 50%;
  left: 45%;
  z-index: 6;
}

.signup__container .carousel li img {
  width: 200px;
  transition: 1s;
}

.signup__container .carousel li.active img {
  width: 300px;
  transition: 1s;
}

.signup__container .carousel li.point1 {
  top: 45%;
  left: 25%;
  z-index: 5;
}

.signup__container .carousel li.point2 {
  top: 35%;
  left: 20%;
  z-index: 4;
}

.signup__container .carousel li.point3 {
  top: 25%;
  left: 32%;
  z-index: 3;
}

.signup__container .carousel li.point4 {
  top: 25%;
  left: 55%;
  z-index: 2;
}

.signup__container .carousel li.point5 {
  top: 35%;
  left: 65%;
  z-index: 4;
}

.signup__container .carousel li.point6 {
  top: 45%;
  left: 60%;
  z-index: 5;
}

@media (max-width : 1023px) {
  .signup__container{
    grid-template-columns: minmax(0 , 1.25fr) 1fr;
  }
  .signup__container .carousel li img {
    width: 150px;
  }
  .signup__container .carousel li.active img{
    width: 220px;
  }

  .login__signup{
    padding: 32px;
  }
}


@media (max-width : 767px) {
    .circular__carosel{
      display: none;
    }

    .signup__container{
      grid-template-columns: 1fr;
    }

    .login__signup{
      padding: 64px;
      min-height: 80vh;
    }
}

@media (max-width : 424px) {
  .login__signup{
    padding: 64px 16px;
  }
}

