}


.main__container{
  display: grid;
  grid-template-columns: repeat(2 , minmax(0,1fr));
  min-height: 100vh;
  background: url(/static/media/background.ff49b081.jpeg);
  background-size: cover;
  background-position: center;
  padding-top: 32px;
  padding: 0 0 0 10%;
}

.heading{
  margin-top: 32px;
  margin-bottom: 32px;
  display: flex;
  justify-content: center;
}

.heading p{
  background-color: #F06F38;
  padding: 16px;
  color : white;
  font-size: 28px;
  border-radius: 20px;
}


