.frame_container {
  margin: auto;
  width: 462.62px;
  height: 663.09px;
  background-image: url(/static/media/ipad-2.138606fb.svg);
  padding: 0;
  /* border: 1px solid black; */
}

.phone {
  width: 400px;
  height: 467px;
  margin: auto;
  position: relative;
  top: 9.5%;
}


.qr_body{
  position: absolute;
  left: 50%;
  top : 50%;
  -webkit-transform: translate(-50% , -50%);
          transform: translate(-50% , -50%);
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  max-width: 300px;
  width: 100%;
  border-radius: 16px;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;
  z-index: 5;

}

.qr_body.active{
  opacity: 1;
  visibility: visible;
  transition: 0.5s;
}

.qr_container{
  background: white;
  padding: 32px;
  padding-top: 1px;
  border-radius: 16px;
  text-align: center;
}

.qr_container .heading{
  text-align: center;
  margin-bottom: 8px;
}

.qr_container .heading h1{
  font-size: 20px;
}

.qr_container .qr_code{
  margin-bottom: 8px;
}

.qr_container .closeQr{
  padding: 8px 16px;
  color : white;
  border : 1px solid #F06F38;
  background: #F06F38;
  border-radius: 8px;
  transition: 0.5s;
}

.qr_container .closeQr:hover{
  color : #F06F38;
  background: white;
  transition: 0.5s;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

.landingPage__main .hero__image {
  text-align: center;
}

.landingPage__main .hero__image img {
  width: 55%;
}

.landingPage__main .hero__section .downArrow img {
  width: 100%;
  margin-top: -20%;
}

.landingPage__main .roadMap {
  margin: 64px;
  text-align: center;
}

.landingPage__main .roadMap img {
  width: 75%;
  margin: auto;
}

.landingPage__main .details {
  width: 70%;
  margin: 100px auto;
  display: grid;
  grid-template-columns: 5fr 3fr;
  justify-content: space-between;
  align-items: center;
}

.landingPage__main .details .info {
  font-style: normal;
  font-weight: normal;
  margin-left: 8vw;
  font-size: 28px;
  line-height: 34px;
}

.landingPage__main .details .logo {
  text-align: center;
}

.landingPage__main .details .logo img {
  width: 175px;
}

.landingPage__main .how__works {
  padding: 32px 64px 128px;
  background-color: #eeeeee;
  text-align: center;
}

.landingPage__main .how__works img {
  width: 70%;
}

.landingPage__main .individuals,
.landingPage__main .schools {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 32px;
}

.landingPage__main .individuals .left__container img {
  width: 400px;
}

.landingPage__main .schools .right__container img {
  width: 400px;
  margin-left: -40px;
}

.landingPage__main .individuals .right__container,
.landingPage__main .schools .left__container {
  padding: 32px;
  background: #eeeeee;
  margin-left: -32px;
  min-height: 350px;
  width: 37%;
  margin-bottom: 32px;
  position: relative;
  z-index: 2;
}

.landingPage__main .individuals .right__container h1,
.landingPage__main .schools .left__container h1 {
  font-weight: 400;
  margin-bottom: 32px;
}

.landingPage__main .individuals .right__container p,
.landingPage__main .schools .left__container p {
  margin-bottom: 16px;
}

.landingPage__main .individuals .right__container a,
.landingPage__main .schools .left__container a {
  color: #00b2ff;
  text-decoration: none;
  display: block;
  margin-top: 32px;
}
.roadMap_mobile {
  display: none;
}

@media (max-width: 1130px) {
  .landingPage__main .hero__image img {
    width: 68%;
  }

  .landingPage__main .roadMap img {
    width: 90%;
  }
  .landingPage__main .details {
    padding-left: 0;
    width: 90%;
  }
  .landingPage__main .details .info {
    font-size: 22px;
  }

  .landingPage__main .how__works img {
    width: 85%;
  }
}

