.frame_container {
  margin: auto;
  width: 462.62px;
  height: 663.09px;
  background-image: url(../../assets/ipad-2.svg);
  padding: 0;
  /* border: 1px solid black; */
}

.phone {
  width: 400px;
  height: 467px;
  margin: auto;
  position: relative;
  top: 9.5%;
}


.qr_body{
  position: absolute;
  left: 50%;
  top : 50%;
  transform: translate(-50% , -50%);
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  max-width: 300px;
  width: 100%;
  border-radius: 16px;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;
  z-index: 5;

}

.qr_body.active{
  opacity: 1;
  visibility: visible;
  transition: 0.5s;
}

.qr_container{
  background: white;
  padding: 32px;
  padding-top: 1px;
  border-radius: 16px;
  text-align: center;
}

.qr_container .heading{
  text-align: center;
  margin-bottom: 8px;
}

.qr_container .heading h1{
  font-size: 20px;
}

.qr_container .qr_code{
  margin-bottom: 8px;
}

.qr_container .closeQr{
  padding: 8px 16px;
  color : white;
  border : 1px solid #F06F38;
  background: #F06F38;
  border-radius: 8px;
  transition: 0.5s;
}

.qr_container .closeQr:hover{
  color : #F06F38;
  background: white;
  transition: 0.5s;
}
