@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;1,500;1,600;1,700&display=swap");

* {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

.landingPage__main .hero__image {
  text-align: center;
}

.landingPage__main .hero__image img {
  width: 55%;
}

.landingPage__main .hero__section .downArrow img {
  width: 100%;
  margin-top: -20%;
}

.landingPage__main .roadMap {
  margin: 64px;
  text-align: center;
}

.landingPage__main .roadMap img {
  width: 75%;
  margin: auto;
}

.landingPage__main .details {
  width: 70%;
  margin: 100px auto;
  display: grid;
  grid-template-columns: 5fr 3fr;
  justify-content: space-between;
  align-items: center;
}

.landingPage__main .details .info {
  font-style: normal;
  font-weight: normal;
  margin-left: 8vw;
  font-size: 28px;
  line-height: 34px;
}

.landingPage__main .details .logo {
  text-align: center;
}

.landingPage__main .details .logo img {
  width: 175px;
}

.landingPage__main .how__works {
  padding: 32px 64px 128px;
  background-color: #eeeeee;
  text-align: center;
}

.landingPage__main .how__works img {
  width: 70%;
}

.landingPage__main .individuals,
.landingPage__main .schools {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 32px;
}

.landingPage__main .individuals .left__container img {
  width: 400px;
}

.landingPage__main .schools .right__container img {
  width: 400px;
  margin-left: -40px;
}

.landingPage__main .individuals .right__container,
.landingPage__main .schools .left__container {
  padding: 32px;
  background: #eeeeee;
  margin-left: -32px;
  min-height: 350px;
  width: 37%;
  margin-bottom: 32px;
  position: relative;
  z-index: 2;
}

.landingPage__main .individuals .right__container h1,
.landingPage__main .schools .left__container h1 {
  font-weight: 400;
  margin-bottom: 32px;
}

.landingPage__main .individuals .right__container p,
.landingPage__main .schools .left__container p {
  margin-bottom: 16px;
}

.landingPage__main .individuals .right__container a,
.landingPage__main .schools .left__container a {
  color: #00b2ff;
  text-decoration: none;
  display: block;
  margin-top: 32px;
}
.roadMap_mobile {
  display: none;
}

@media (max-width: 1130px) {
  .landingPage__main .hero__image img {
    width: 68%;
  }

  .landingPage__main .roadMap img {
    width: 90%;
  }
  .landingPage__main .details {
    padding-left: 0;
    width: 90%;
  }
  .landingPage__main .details .info {
    font-size: 22px;
  }

  .landingPage__main .how__works img {
    width: 85%;
  }
}

@media (max-width: 1000px) {
  .landingPage__main .roadMap {
    margin: 64px 16px 64px 32px;
  }
  .landingPage__main .roadMap img {
    width: 100%;
  }

  .landingPage__main .details {
    margin: 0;
    margin-bottom: 10px;
  }

  .landingPage__main .how__works {
    padding: 32px;
  }

  .landingPage__main .individuals,
  .landingPage__main .schools {
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 32px;
  }

  .landingPage__main .individuals .left__container img {
    width: 60%;
  }
  .landingPage__main .individuals .right__container {
    margin: auto;
    width: 60%;
    margin-top: -10px;
  }

  .landingPage__main .schools .left__container {
    grid-row: 2/3;
    margin: auto;
    width: 60%;
    margin-top: -10px;
  }
  .landingPage__main .schools .right__container img {
    width: 60%;
    margin-left: 0;
  }
}

@media (max-width: 767px) {
  .landingPage__main .hero__image img {
    width: 100%;
    text-align: left;
    margin-left: -72px;
  }

  .landingPage__main .details {
    grid-template-columns: 1fr;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 95%;
    margin: auto;
    margin-bottom: 10px;
  }

  .landingPage__main .details .info {
    margin-left: 0;
  }

  .landingPage__main .details .logo {
    grid-row: 1/2;
    margin-bottom: 16px;
  }

  .landingPage__main .how__works {
    padding: 16px 8px;
  }
  .landingPage__main .how__works img {
    width: 95%;
  }

  .landingPage__main .individuals .left__container img,
  .landingPage__main .individuals .right__container,
  .landingPage__main .schools .left__container,
  .landingPage__main .schools .right__container img {
    width: 80%;
  }
}

@media (max-width: 600px) {
  .landingPage__main .hero__image img {
    width: 95%;
    margin-left: 0;
  }

  .landingPage__main .hero__section .downArrow img {
    display: none;
  }
  .hero__section {
    height: 52vh;
    display: flex;
    place-items: center;
    justify-items: center;
  }
  .roadMap {
    width: 100%;
    height: fit-content;
    margin: 2px !important;
  }
  .roadMap > img {
    display: none;
  }
  .roadMap_mobile {
    display: flex;
    flex-direction: column;
    margin: 0 10px;
    padding: 10px;
    align-items: center;
  }
  .roadMap_mobile > img {
    margin: 30px 0px;
    width: 80%;
  }
}
